<script>

	import Nav from "./NavBar.svelte"
	import IntroText from "./IntroText.svelte"
	import Grid from "./Grid.svelte"
	import ProjectPage from "./ProjectPage.svelte"

	import { onMount } from 'svelte';

	let projects_path = 'https://raw.githubusercontent.com/PrattSAVI/WebThings/main/Project.json'
    let path = 'https://raw.githubusercontent.com/PrattSAVI/WebThings/main/imgs/'
    let projects;
    let arr =[];
	let visible = false;

	onMount(async () => {
		const res = await fetch( projects_path );
		projects = await res.json();
        arr = Object.values( projects )

		//Sort by Code Columns
		arr.sort(function(a, b){
			return a.code - b.code;
		});

		//Filter Null lines
		arr = arr.filter( function(d){
			return !!d['name'];
		})

        console.log( arr )
	});

	let active_project;
	function handleMessage(event) {
		active_project = event.detail.text;
		visible = true;
	}

	function handleClose(event) {
		active_project = null;
		visible = false;
	}

	function NavClick(){
		visible = false;
	}


</script>

<main>

	<!-- <Nav on:click={NavClick}/> -->

	<div class="container">
		<IntroText />
		{#key active_project}
			<ProjectPage {visible} {active_project} {arr} on:close={handleClose}/>
		{/key}
		<hr>
		<Grid {arr} on:message={handleMessage}/>
	</div>


</main>

<style>

	.container{
		padding-left: 75px;
		padding-right: 60px;
		max-width: 1100px;
		justify-content:center;
		margin:120px auto;
	}

	@media (max-width: 520px) { 
        .container{
			padding-left: 35px;
			padding-right: 35px;
			margin:70px auto;
            }
    }

</style>