
<script>
    import { slide } from 'svelte/transition';
    import { fade } from 'svelte/transition';
    import Carousel from 'svelte-carousel'
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();

    let path = 'https://raw.githubusercontent.com/PrattSAVI/WebThings/main/imgs/'

    export let active_project;
    export let arr;
    export let visible;

    let active = arr.filter( function(d){
        return d.name === active_project;
    })

    let active1 = active[0]
    //console.log( active1 )

    //Get Photos in a single array
    let img_cols = ["img1","img2","img3","img4","img5","img6","img7","img8","img9","img10"]

    function xClick(){
        //visible = false

        dispatch('close', {
			text: close
		});
    }


</script>


{#if visible}
    <div transition:slide="{{ y: 100, duration: 1000 }}" class="project-container">
        
        <hr />
        <div transition:fade class="close" on:click={xClick}>&#10006</div>

        <div class="project-content" style="white-space: pre-line">
            <h3>{active1['name']}</h3>

            <p class="project-text">
                {active1['text']}
            </p>

            <div class="contentA">

                <div class="photo">
                    <Carousel
                            let:showPrevPage
                            let:showNextPage
                        >
    
                            <div slot="prev" on:click={showPrevPage} class="custom-arrow custom-arrow-prev">
                                <i>&#10094;<i />
                            </div>
    
                            {#each img_cols as col}
                                {#if active1[col] }
                                    <img class="container-photos" alt="test" src= {path + active1[col]} />  
                                {/if}
                            {/each}
    
                            <div slot="next" on:click={showNextPage} class="custom-arrow custom-arrow-next">
                                <i>&#10095;<i />
                            </div>
    
                    </Carousel>
                </div>

                <div class="items">
                    
                    <h5>Year: </h5>
                    <p class="project-item">{active1['year']}</p>

                    <h5>Team: </h5>
                    <p class="project-item">{active1['team']}</p>

                    <h5>Client | Partner: </h5>
                    <p class="project-item">{active1['client']}</p>

                    <h5>Roles: </h5>
                    <p class="project-item">{active1['roles']}

                    <h5>Tools: </h5>
                    <p class="project-item">{active1['tools']}</p>

                    <h5>Project Link: </h5>
                    <p class="project-item"><a href="{active1['link']}">Click here to view the project</a></p>

                </div>

            </div>

        </div>

    </div>
    
{:else}
    <div class="empty-container"></div>
{/if}


<style>

    @media (max-width: 760px) { 
            .contentA{
                flex-direction: column !important;
                }
            .photo{
                width:100% !important;
            }
        }

    .contentA{
        margin-top: 10px;
        display: flex;
        flex-direction: row;
    }

    .photo{
        display: flex;
        width:66%;
        margin-right: 30px;
        margin-top: 15px;
    }

    .photo img{
        max-height: 750px;
        max-width: 100%;
        object-fit: contain;
    }

    .items{
        padding-top: 5px;
        width:33%;
    }

    .project-content{
        margin-top: -10px;
    }

    .project-text{
        font-size: 12pt;
        color: #3D3D3D;
        font-family: 'Open Sans', sans-serif;
        line-height: 1.5;
    }

    .project-item{
        margin:0;
        font-size: 10pt;
        font-weight: 300;
        color: #3D3D3D;
        font-family: 'Open Sans', sans-serif;
        line-height: 1.5;
    }

    .project-container{
        width: 100%;
        padding-bottom: 25px;
    }

    .empty-container{
        height: 0px;
    }

    .close{
        font-size: 20px;
        width:100%;
        text-align: right;
        color: var(--acolor);
        cursor: pointer;
        
    }

    .project-text{
        margin:0;
    }

    .custom-arrow{
    font-family: Arial, Helvetica, sans-serif;
    font-style: normal;
    color:#777;
    font-size:24pt;
    font-weight: 900;
    width:30px;
    height:100%;
    position:absolute;
    z-index:3;
    background:rgba(255,255,255,0.15)!important;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.custom-arrow.custom-arrow-next{
    right:2px;
}
	
.custom-arrow.custom-arrow-prev{
    left:2px
}

i{
    font-style: normal!important;
}
</style>