<script>
    import { onMount } from 'svelte';
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();

    export let arr;
    let path = 'https://raw.githubusercontent.com/PrattSAVI/WebThings/main/imgs/'


    function handleClick(e) {
		dispatch('message', {
			text: e.target.id
		});
	}


</script>

<div class="grid-container">

    {#each arr as proj }
        <div class="grid-cell" id={proj['name']} style="background-image:url({path + proj['cover']});" on:click={handleClick}>
            <div class="back">
                <div class="cell-text" >
                    <h3 style="margin-bottom: 2px;"> {proj['name']} </h3>
                    <p class="sub-title"> {proj['subtitle']} </p>
                </div>
            </div>
        </div>
    {/each}

</div>

<style>
    .grid-container{
        margin-top: 30px !important;
        display: flex;
        flex-flow: row wrap;
    }

    .grid-container>* {
        flex: 1;
        margin: 10px;
        flex-basis: 25%;
      }

    .grid-cell{
        min-width:230px;
        height:200px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        filter: grayscale(100%);
        cursor: pointer;
    }

    .grid-cell:hover{
        transition: 300ms linear;
        filter: grayscale(0%);
    }

    .back{
        display: inline-block;
        width:100%;
        height: 100%;
        background-color: rgba(255,255,255,0.65);
        pointer-events: none;
        display: flex;
        justify-content: center;
        align-items: center; 
    }


    .cell-text{
        
        width:100%;
        justify-content: center;
        text-align: center;
    }

    .sub-title{
        margin-top: 0px!important;
        color: #333333;
        font-size: 12px;
    }


</style>