        <div class="column-right">
            <p>Hello! This is Can <i>(pron. John)</i> Sucuoğlu.</p>

            <p>
                Trained as an architect and designer, I specialize in data visualization and mapping. As the Interim Director of <a href="https://commons.pratt.edu/savi/">Spatial Analysis and Visualization Initiative</a> at the <a href="https://www.pratt.edu/">Pratt Institute</a> at Brooklyn, NY.
                 I work on community oriented data analytics projects with a focus on visual communication of information to the public at large. Since 2019, I have been an organizing member of <a href="https://datathroughdesign.com/">Data Through Design</a>, 
                 an annual exhibition that brings together work exploring artistic interpretations of information. 
                 I am also a co-founder of <a href="http://bitsnbricks.com/">Bits‘n Bricks</a>, 
                 a research group which provides consultancy on data-based design services and investigates the potential of emerging digital technologies in improving urban environments.
            </p>

